/* Set defaults */

html,
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', 'Open Sans', 'Arial', sans-serif;
  max-width: 1920px;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
}

.btn-reset {
  appearance: none;
  border-radius: 0;
  opacity: 1;
  border: none;
  background-color: transparent;
  font: inherit;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 10px;
  height: 5px;
  background-color: #eee;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #000;
}

.ul-reset {
  list-style: none;
}

.input {
  font: inherit;
  appearance: none;
  opacity: 1;
  transition: 0.5s;
}

.input:focus {
  outline: none;
  box-shadow: 1px 1px 30px 1px rgba(60, 86, 166, 0.3);
}

img,
video {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

/* Atomic */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

.d-f {
  display: flex;
}

.ttu {
  text-transform: uppercase;
}

.ta-c {
  text-align: center;
}

.ov-h {
  overflow: hidden;
}

.ov-s {
  overflow: scroll;
}

.black {
  color: #000;
}

/* Global */

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* General */

@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}